.login{
  position: absolute;
  height:450px;
  width:300px;
  top: 50px;
  left: calc(50% - 175px);
  z-index:99;
  background-color:#351818;
  padding: 30px;
  padding-top: 10px;
  overflow: hidden;
  -webkit-animation: comein 1s linear;
          animation: comein 1s linear;
  overflow: hidden !important;
}



@-webkit-keyframes comein {
  from{
    opacity:0;
    transform:translateY(50px)
  }
  to{
    opacity:1;
    transform:translateY(0px)
  }
}



@keyframes comein {
  from{
    opacity:0;
    transform:translateY(50px)
  }
  to{
    opacity:1;
    transform:translateY(0px)
  }
}

.login h1{
margin-bottom: 30px;
}

.login .form {
width: 90%;
max-width: 360px;
margin: auto;
margin-top: 10px;
text-align: center;
font-family: 'Roboto';
position: relative;
}

.login .form img{
height: 150px;
width: 100px;
top: 180px;
left: -60px;
position: absolute
}
