.game-area{
  background-color: black;
  position: relative;
  /* margin: 50px auto; */
  /* margin-top: 0px; */
  width: 400px;
  height: 400px;
  border: 2px solid #000;
  z-index:0;
}
.snake-dot{
  position: absolute;;
  width: 2%;
  height: 2%;
  background-color: green;
  border: 1px solid black;
}

.snake-food{
  position: absolute;
  width: 2%;
  height: 2%;
  background: red;
  border:1px solid black;
  z-index: 1;
  border-radius: 50%;
}

.start{
  position: absolute;
  top: 50%;
  left: 50%;
  height: 50px;
  width: 50px;
  transform: translate(-25px,-25px);
  /* box-shadow:0px 0px 20px 200px rgba(158, 23, 23, 0.533); */
  z-index:5;
  border:none;
  outline:none;
  background-color:#2a181899;
}


.start svg{
  height:100%;
  width:100%;
  color:#ffa500;

  cursor:pointer;
}

.start::after{
  content:'';
  position: absolute;
  top: -175px;
  left: -175px;
  background-color: #2a181899;
  height: 400px;
  width:400px;
  z-index:-1
}

.game button{
  display: flex;
  justify-content:center;
  padding:0px;
  cursor:pointer;
  outline:none;
}

.game-area{
  margin: auto;
}

#settings {
  display: flex;justify-content: center;
}

#settings button{
  background:orange;
  border:none;
  margin:2px;
}
#fade{
  
  -webkit-animation: faded .2s ease-in;
  
          animation: faded .2s ease-in;
}

.scoreboard_body{
  display: flex;
  flex-wrap: wrap;
  justify-content: center
}

@-webkit-keyframes faded {
  from {opacity: 0}
  to {opacity: 1}
}

@keyframes faded {
  from {opacity: 0}
  to {opacity: 1}
}

.scoreboard *{
  /* color:black;
  background-color:#c6a1a1; */
   -webkit-animation: faded 2s ease;
           animation: faded 2s ease 
}

.scoreboard svg{
  height: 50px;
  width: 50px;
  color:orange;
  margin-bottom:25px;
}

.scoreboard_body div{
  min-height:400px;
  border:1px solid black;
  padding:2px;
  margin:12px;
  background-color: #251111;
  width: 500px;
}

.scoreboard_body div ol{
  margin-left: 30px;
}
.scoreboard h1{
  color:#d7a8a8;
  margin-bottom:1px;
  /* padding:10px; */
}

ol{
  /* padding:10px; */
  list-style: none;
  counter-reset: my-awesome-counter;
}

ol li{
  counter-increment:my-awesome-counter;
}

ol li::before{
  content: counter(my-awesome-counter) ". ";
  color: green;
  font-weight: bold;
}

ol li:nth-child(3)~li::before{
  color: white;
}
